body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input,
button {
	 font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

:root {
	--base-color: #009688;
	--shade04: #b2dfdb;
	--shade03: #80cbc4;
	--shade02: #26a69a;
	--shade01: #4db6ac;
	--shade10: #00897b;
	--shade20: #00796b;
	--shade30: #00695c;
	--shade40: #004d40;
	--dark-color: #001f1a;
	--dark-alt-color: #343434;
	--light-color: #e0f2f1;
	--light-alt-color: #f9f9f9;
	--light-transparent: #f9f9f9a0;


	--nav-height: 70px;

	--text-color: black;
}

.bd_posts {
	padding: 1rem;
}

.bd_posts h1 {
	font-weight: normal;
	text-transform: capitalize;
}
.mk-post {
	text-decoration: none;
}

.mk-post div {
	width: 800px;
	max-width: calc(100% - 10px);
	margin: 20px auto;
	border-radius: 20px;
	display: flex;
	flex-flow: wrap row;
	align-items: center;
	justify-content: space-around;
}

.mk-post div img {
	width: auto;
	height: auto;
	max-width: 450px;
	max-height: 250px;
}

.mk-post div h1 {
	font-weight: normal;
	color: black;
}

.pg-ld {
	background: url(/Joshua0707/postbuilder/static/media/posts.218c3990.png) no-repeat top center;
	background-size: cover;
	height: calc(100vh - var(--nav-height));
	width: calc(100vh - 30px);
}

nav.nv-wr {
	width: 100%;
	height: var(--nav-height);
	background: var(--light-alt-color);
	box-shadow: 1px 1px 3px #ddd, 3px 3px 5px #eee;
}

nav.nv-wr a {
	text-decoration: none;
		color: #1b1b1b;
}

nav.nv-wr .nv-bd {
	display: inline-block;
	line-height: var(--nav-height);
	margin-left: 20px;
	margin-right: 50px;
	font-size: 22px;
}

nav.nv-wr .nv-ut {
	float: right;
}

nav.nv-wr .nv-ut a, .dp-dn {
	display: inline-block;
	margin-right: 15px;
	line-height: var(--nav-height);
}

nav.nv-wr .nv-ut a svg, .dp-dn svg {
	margin-right: 7px;
}

nav.nv-wr .nv-ut .dp-dn {
	position: relative;
}

nav.nv-wr .nv-ut .dp-dn span {
	color: #1b1b1b;
	cursor: pointer;
}

nav.nv-wr .nv-ut .dp-dn div {
	min-width: 160px;
	position: absolute;
	top: 80px;
	right: 0px;
	background: var(--light-alt-color);
	box-shadow: 1px 1px 3px #ddd, 3px 3px 5px #eee;
}

nav.nv-wr .nv-ut .dp-dn div a {
	width: 130px;
	max-width: 100%;
	margin: auto;
	display: block;
	box-sizing: border-box;
	padding: 7px;
	line-height: normal;
	line-height: initial;
	color: #1b1b1b;
}

@media (max-width: 580px) {
	nav.nv-wr .nv-ut {
		background-color: var(--light-color);
		width: 100%;
		height: calc(100vh - var(--nav-height));
		box-sizing: border-box;
		transform: translateX(100%);
		position: absolute;
		transition: ease-in-out 300ms transform;
		z-index: 50;
	}
	nav.nv-wr .nv-ut.show {
		transform: translate(0);
	}
	nav.nv-wr .nv-ut a {
		display: block;
		width: 200px;
		max-width: 100%;
		margin: auto;
	}
	nav.nv-wr .nv-ut .dp-dn {
		display: block;
		margin: 10px auto auto auto;
		padding-top: 10px;
		line-height: normal;
		border-top: var(--shade02) thin solid;
	}
	nav.nv-wr .nv-ut .dp-dn span {
		display: none;
	}
	nav.nv-wr .nv-ut .dp-dn div {
		position: relative;
		background: transparent;
		box-shadow: none;
		width: 100%;
		top: 0;
		right: 0;
	}
	nav.nv-wr .nv-ut .dp-dn div a {
		display: block;
		width: 200px;
		max-width: 100%;
		margin: auto;
		text-align: left;
		line-height: var(--nav-height);
		padding: 0;
	}
}

nav.nv-wr .nv-tg-bx {
	display: inline-flex;
	align-items: center;
	height: 100%;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	float: right;
	margin-right: 10px;
}

nav.nv-wr .nv-tg {
	cursor: pointer;
	display: inline;
}

nav.nv-wr .nv-tg span {
	height: 2px;
    width: 20px;
    margin: 5px;
    display: block;
    background-color: #333;
	transition: transform ease .3s;
}

nav.nv-wr .nv-tg.close span:nth-child(1) {
	transform: translateY(3.5px) rotateZ(45deg);
}

nav.nv-wr .nv-tg.close span:nth-child(2) {
	transform: translateY(-3.5px) rotateZ(-45deg);
}



.login-pg,
.register-pg {
	padding-top: 10%;
	box-sizing: border-box;
}

.login-pg header,
.register-pg header {

}

.login-pg header h1,
.register-pg header h1 {
	font-weight: 500;
	font-size: 2.5rem;
	text-align: center;
}

.login-pg header p,
.register-pg header p {
	text-align: center;
	font-size: 17px;
}

.login-pg header span.cust-emoji {
	direction: ltr;
}

form.user-form {
	width: 600px;
	max-width: calc(100% - 20px);
	margin: auto;
}

form.user-form .form-rl {
	display: flex;
	flex-direction: column;
	align-items: center;
}

form.user-form div.input-vw {
	position: relative;
	min-width: 400px;
	outline: none;
	border: none;
	transition: background ease-in-out 300ms;
	margin: 10px auto;
}

form.user-form div.input-vw input {
	width: 400px;
	max-width: 100%;
	height: 100%;
	padding: 25px 10px 10px 45px;
	outline: none;
	border: none;
	box-sizing: border-box;
	transition: border ease-in-out 300ms;
	background: var(--light-alt-color);
	font-size: 16px;
}

form.user-form div.input-vw input[type=password] {
	padding-right: 60px;
}

form.user-form div.input-vw label {
	position: absolute;
	left: 45px;
	top: 0;
	bottom: 0;
	padding: .5rem 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	pointer-events: none;
	transition: all ease-in-out 300ms, justify-content ease-in-out 300ms;
	color: #898989;
}

form.user-form div.input-vw input:focus + label,
form.user-form div.input-vw input:valid + label {
	justify-content: flex-start;
	font-size: .7rem;
}

form.user-form div.input-vw span.forget {
	position: absolute;
	right: 0;
	top: 0;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-right: 10px;
}

form.user-form div.input-vw span.forget a {
	text-decoration: none;
	font-size: 14px;
	color: var(--base-color);
}

form.user-form div.input-vw span.ico {
	position: absolute;
	left: 0;
	top: 0;
	height: 100%;
	width: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
}

form.user-form div.input-vw span.ico svg {
	color: #898989;
}

form.user-form div.input-sb {
	width: 400px;
	max-width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.register-pg form.user-form div.input-sb {
	justify-content: center;
}

form.user-form div.input-sb a {
	text-decoration: none;
	color: var(--shade30);
}

form.user-form div.input-sb button {
	background-color: var(--base-color);
	border: none;
	color: white;
	padding: 12px 25px;
	margin: 10px 3px;
	cursor: pointer;
	font-size: 15px;
	outline: none;
	transition: background ease-in-out 300ms;
}

form.user-form div.input-sb button:focus,
form.user-form div.input-sb button:hover {
	background-color: var(--shade10);
}

.register-pg form.user-form div.input-sb button {
	/*border-radius: 50px;*/
}

.register-pg form.user-form div.alt-div {
	width: 400px;
	max-width: 100%;
	margin-top: 10px;
}

.register-pg form.user-form div.alt-div a {
	text-decoration: none;
	color: var(--shade30);
}
